#techno-card-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2em;
}
#techno {
  display: flex;
  justify-content: space-around;
  width: 80%;
  position: relative;
}
#techno-decoration {
  position: absolute;
  height: 5px;
  width: 50px;
  top: 40%;
  background-color: #ffd500;
  transform: rotate(135deg);
}
.techno-card {
  text-align: center;
}
.techno-card-list {
  color: black;
  font-family: 'Roboto', sans-serif;
  line-height: 150%;
}
